import './style.css'
import data from './cars.json' assert { type: 'json' };
console.log(data.data[0].name, data.data[0].type, data.data[0].img, data.data[0].volume, data.data[0].persons, data.data[0].cost, data.data[0].w, data.data[0].h);


function cars(x0, x1, y){
    for (let i=x0; i<x1; i++){
        let w = data.data[i].w;
        console.log(w);
        let h = data.data[i].h;
        console.log(h);
        let exco;
        data.data[i].extracost == 0 ?  exco = '' : exco = `<p class="text-[16px] line-through C90A3BF">$${data.data[i].extracost}.00</p>`;
        let pattern = ` <div class="w-[304px] h-[388px] p-[24px] flex flex-col justify-center items-center bg-white rounded-[10px] ">
                            <div class="flex justify-between w-full">
                                <div class="flex flex-col justify-start items-start">
                                <p class="font-semibold text-2xl">${data.data[i].name}</p>
                                <p class="C90A3BF font-semibold">${data.data[i].type}</p>
                                </div>
                                <img src="/public/heart1.svg" id="like${i}" class="mt-[5px] cursor-pointer w-[24px] h-[24px]" alt="">
                            </div>
                            <img src="/public/${data.data[i].img}.svg" class="mt-[64px] w-[242px]! h-[92px]!" alt="">
                            <div class="mt-[64px] flex gap-[16px]">
                                <div class="flex gap-[6px]">
                                <img src="/public/volume.svg" class="w-[24px] h-[24px]" alt="">
                                <p class="C90A3BF text-[15px]">${data.data[i].volume}L</p>
                                </div>
                                <div class="flex gap-[6px]">
                                <img src="/public/manual.svg" class="w-[24px] h-[24px]" alt="">
                                <p class="C90A3BF text-[15px]">manual</p>
                                </div>
                                <div class="flex gap-[6px]">
                                <img src="/public/profile-2user.svg" class="w-[24px] h-[24px]" alt="">
                                <p class="C90A3BF text-[15px]">${data.data[i].persons} People</p>
                                </div>
                            </div>
                            <div class="mt-[24px] w-full flex justify-between">
                                <div class="flex flex-col">
                                <p class="font-semibold text-[20px]">$${data.data[i].cost}.00/ <hui class="text-[16px] C90A3BF">day</hui></p>
                                ${exco}
                                </div>
                                <button class="w-[116px] h-[44px] C3563E9BGBUTTON rounded-[4px] font-semibold text-white">Rent Now</button>
                            </div>
                        </div>` 
        document.getElementById(y).innerHTML += pattern
    }
}
cars(0, 4, 'vetrina')
cars(4, 12, 'vetrina2')

function likes(id){
    console.log(document.getElementById(id).src);
    
    document.getElementById(id).src == `http://localhost:5173/public/heart1.svg` ? document.getElementById(id).src = `/public/heart2.svg` : document.getElementById(id).src = `/public/heart1.svg`    
}
for (let i=0; i < 12; i++){
    const hertbutton = document.getElementById(`like${i}`);
    hertbutton.addEventListener('click', () => {likes(`like${i}`)})
}

